import InsetLogin from '../../components/InsetLogin'
import request from '../../util/request'
import convertDate from '../../util/convertDate'

let currentDate = new Date()

export default {
    name: 'Bookcase',
    components: { InsetLogin },
    data () {
        return {
            modalName: '',
            listTouchStart: 0,
            listTouchDirection: null,
            // 假数据
            result: [],
            deleteModal: false,
            toDeleteInfo: {}
        }
    },
    computed: {
        userInfo () {
            return this.$store.state.userInfo
        },
        sortType () {
            return this.$store.state.sortType
        }
    },
    onLoad () {
        if (this.userInfo) {
            uni.startPullDownRefresh()
        }
    },
    watch: {
        // 主要用于 一开始未登录，后来登录；重新加载数据
        userInfo (newVal, oldVal) {
            if (newVal && !oldVal) {
                uni.startPullDownRefresh()
            }
        },
        sortType (newVal, oldVal) {
            this.queryBookcase()
        }
    },
    onPullDownRefresh () {
        if (this.userInfo) {
            this.queryBookcase()
        } else {
            uni.stopPullDownRefresh()//得到数据后停止下拉刷新
        }
    },
    methods: {
        // ListTouch触摸开始
        touchStart (e) {
            this.listTouchStart = e.touches[0].pageX
        },
        // ListTouch计算方向
        touchMove (e) {
            this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
        },
        // ListTouch计算滚动
        touchEnd (e) {
            if (this.listTouchDirection === 'left') {
                this.modalName = e.currentTarget.dataset.target
            } else {
                this.modalName = null
            }
            this.listTouchDirection = null
        },
        startReading(item) {
            uni.navigateTo({ url: '/pages/reading/SimpleRead?novels=' + JSON.stringify(item)})
        },
        navChange () {
            uni.switchTab({
                url: '/pages/navigation/Classify',
            })
        },
        convertDate (updateTime) {
            return convertDate.convertZh(currentDate, updateTime)
        },
        queryBookcase () {
            let params = {
                condition: {
                    uniqueId: this.userInfo.uniqueId,
                    sortType: this.sortType
                }
            }
            request.post('/relation/bookcase', params).then(data => {
                if (data.status === 200 && data.total > 0) {
                    this.result = data.data
                }
            }).finally(() => {
                uni.stopPullDownRefresh()//得到数据后停止下拉刷新
            })
        },
        topBtn (novelsId) {
            let params = {
                condition: {
                    uniqueId: this.userInfo.uniqueId,
                    novelsId: novelsId
                }
            }
            uni.showLoading({
                title: 'loading...',
                mask: true
            })
            request.post('/relation/topBookcase', params).then(data => {
                if (data.status === 200) {
                    let temp = []
                    this.result.forEach(item => {
                        if (item.novelsId === novelsId) {
                            temp.unshift(item)
                        } else {
                            temp.push(item)
                        }
                    })
                    this.result = temp
                }
            }).finally(() => {
                uni.hideLoading()
            })
        },
        deleteBtn (novelsId, index) {
            this.deleteModal = true;
            this.toDeleteInfo = {
                novelsId: novelsId,
                index: index
            }
        },
        hideDeleteModalBtn() {
            this.deleteModal = false;
            this.toDeleteInfo = {};
        },
        sureDeleteBtn() {
            this.deleteModal = false;
            let params = {
                condition: {
                    uniqueId: this.userInfo.uniqueId,
                    novelsId: this.toDeleteInfo.novelsId
                }
            }
            uni.showLoading({
                title: '删除中',
                mask: true
            })
            request.post('/relation/deleteBookcase', params).then(data => {
                if (data.status === 200) {
                    this.result.splice(this.toDeleteInfo.index, 1)
                }
            }).finally(() => {
                uni.hideLoading();
                this.toDeleteInfo = {};
            })
        }
    }
}
